import React, { Component } from 'react'
import withStyles from '@material-ui/core/styles/withStyles';
import { withRouter } from 'react-router-dom';

// List
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Radio from '@material-ui/core/Radio';
import { RadioGroup } from '@material-ui/core';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import Collapse from '@material-ui/core/Collapse';

// Redux
import {connect} from 'react-redux'
import {choose} from '../redux/actions/List'

const styles = theme => ({
    TrainE:{
        backgroundColor: '#34eb6e',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center'
    },
    TrainF:{
        backgroundColor: '#e34242',
        width: '140px',
        height:'80px',
        fontFamily: 'Lora',
        fontSize: "40px",
        justifyContent: 'center',
        textAlign: 'center'
    },list:{
       color: '#48bffa'
    }
})

class ListCities extends Component {
   
   constructor(props){
      super(props);
      this.state = {
         SetOpen: false,
         open: false,
      }
      this.handleClick = this.handleClick.bind(this)
      this.ChangeOption = this.ChangeOption.bind(this)
   }
   ChangeOption(event){
      let target = event.target.value
      console.log("changeOption!!!")
      this.props.choose(target)
   }
   // Origin 
   handleClick(){
      localStorage.setItem('type', 'Origin');
      this.setState({
         setOpen: !this.state.open,
         open: !this.state.setOpen
      })
   }
   
   
   render(){
      const { classes } = this.props;
      const { listCities } = this.props;
      console.log("classe of list-->> ",listCities)
      //console.log("classe of list-->> ",listCities)
      //this.props.openList('hallo')
      return(
         <List justify="center" className={classes.list}>
            <ListItem button onClick={this.handleClick}>
               <ListItemText primary={this.props.type}/>
               {this.state.open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={this.state.open} timeout="auto" unmountOnExit>
               <List component="div" disablePadding className={classes.list}>
                  <RadioGroup>
                     <ListItem className={classes.nested} >
                        <FormControlLabel control={ <Radio className={classes.list} onChange={this.ChangeOption} value="bogota" name={"algo"}/>}
                           label="Bogotá"/>
                     </ListItem>
                     <ListItem className={classes.nested}>
                        <FormControlLabel control={ <Radio className={classes.list} onChange={this.ChangeOption} value="santa marta"/>}
                           label="Santa Marta"/>
                     </ListItem>    
                     <ListItem className={classes.nested}>
                        <FormControlLabel control={ <Radio className={classes.list} onChange={this.ChangeOption} value="barranquilla"/>}
                           label="Barranquilla"/>
                     </ListItem>
                     <ListItem className={classes.nested}>
                        <FormControlLabel control={<Radio className={classes.list} onChange={this.ChangeOption} value="cartagena"/>}
                           label="Cartagena"/>
                     </ListItem>
                     <ListItem className={classes.nested}>
                        <FormControlLabel control={ <Radio className={classes.list} onChange={this.ChangeOption} value="dorada"/>}
                           label="Dorada"/>
                     </ListItem>
                     <ListItem className={classes.nested}>
                        <FormControlLabel control={ <Radio className={classes.list}onChange={this.ChangeOption} value="belencito"/>}
                           label="Belencito"/>
                     </ListItem>
                  </RadioGroup>
               </List>
            </Collapse>
         </List>
      )
   }
}

const mapStateToProps = (state) => {
   return {
      listCities: state.ListCities,
   };
};

const mapDispatchToProps = (dispatch) => {
   return{
      choose: (text) => {dispatch(choose(text))}
   }
}

export default withRouter(withStyles(styles)(connect(mapStateToProps,mapDispatchToProps)(ListCities)));